import * as React from "react";
import { styled } from "@mui/material/styles";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableContainer from "@mui/material/TableContainer";
import TableCell from "@mui/material/TableCell";

import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    backgroundColor: "#42a5f5",
    color: theme.palette.common.white,
}));

export default function BasicTable({ keyString, value }) {
    return (
        <Grid item xs={12} style={{ display: "table", tableLayout: "fixed", width: "100%" }}>
            <TableContainer component={Paper}>
                <Table>
                    <TableHead>
                        <StyledTableRow>
                            <TableCell style={{ color: "white" }}>Key</TableCell>
                            <TableCell style={{ color: "white" }}>Value</TableCell>
                        </StyledTableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow>
                            <TableCell>{keyString}</TableCell>
                            <TableCell>{value}</TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </TableContainer>
        </Grid>
    );
}
